import { ForwardedRef, ReactNode, forwardRef } from "react"
import { styled } from "@rp/react/web/mui/core"
import { SCROLLER_ROOT_CLASSNAME } from "../../constants"

type RpJsonScrollWrapperProps = { children: ReactNode; className?: string }

const RpJsonScrollWrapperStyled = styled("div")({
  height: "100%",
  overflowY: "auto",
  overflowX: "hidden",
})

export const RpJsonScrollWrapper = forwardRef(
  ({ children, className = "" }: RpJsonScrollWrapperProps, ref: ForwardedRef<HTMLDivElement>) => {
    return (
      <RpJsonScrollWrapperStyled ref={ref} className={`${className} ${SCROLLER_ROOT_CLASSNAME}`}>
        {children}
      </RpJsonScrollWrapperStyled>
    )
  },
)
